function table_v_generator(textinput){

var old = JSON.stringify(textinput).replace(undefined, ''); //convert to JSON string
var textinput = JSON.parse(old); //convert back to array

if(!textinput.add_){
var table_v=`
<table class="tg">
<thead>
  <tr>
    <th class="tg-4f0n">مشخصات</th>
    <th class="tg-4f0n">متغیرها</th>
  </tr>
</thead>
<tbody>
  <tr>
    <td class="tg-34fe">${textinput.title?textinput.title:"-"}</td>
    <td class="tg-2yfi">نام شرکت</td>
  </tr>
  <tr>
    <td class="tg-yj5y">${textinput.id?textinput.id:"-"}</td>
    <td class="tg-tdcm">شناسه‌ ملی شرکت</td>
  </tr>
  <tr>
    <td class="tg-34fe">${textinput.Rev_Billio>0?Math.round(textinput.Rev_Billio):"-"}</td>
    <td class="tg-2yfi">درامد سال ۹۹</td>
  </tr>
  <tr>
    <td class="tg-yj5y">${textinput.Arz_Impo_1?Math.round(textinput.Arz_Impo_1):"-"}</td>
    <td class="tg-tdcm">میزان ارز دولتی (هزار دلار)</td>
  </tr>
  <tr>
    <td class="tg-34fe">${textinput.Arz_Export?Math.round(textinput.Arz_Export):"-"}</td>
    <td class="tg-2yfi">میزان صادرات سالانه (هزار دلار)</td>
  </tr>
  <tr>
    <td class="tg-yj5y">${textinput.TC_AVAIL=="1"?"بله":"-"}</td>
    <td class="tg-tdcm">برخورداری از کارت بازرگانی</td>
  </tr>
  <tr>
    <td class="tg-34fe">${textinput.UNI_CAT?textinput.UNI_CAT:"-"}</td>
    <td class="tg-2yfi">رشته فعالیت</td>
  </tr>
  <tr>
    <td class="tg-yj5y">${textinput.CAPITAL_AM?textinput.CAPITAL_AM:"-"}</td>
    <td class="tg-tdcm">آخرین سرمایه ثبتی</td>
  </tr>
  <tr>
    <td class="tg-34fe">${textinput.نوع_ث?textinput.نوع_ث:"-"}</td>
    <td class="tg-2yfi">نوع ثبتی</td>
  </tr>
  <tr>
    <td class="tg-yj5y">${textinput.تاریخ?textinput.تاریخ:"-"}</td>
    <td class="tg-tdcm">تاریخ ثبت</td>
  </tr>
  <tr>
    <td class="tg-34fe">${textinput.Final_Scor?textinput.Final_Scor:"-"}</td>
    <td class="tg-2yfi">امتیاز نهایی</td>
  </tr>
</tbody>
</table>
`
}
else {
  table_v='شعبه بانک سامان'+'<br>'+textinput.add_;
}
return table_v;
}


function customMarkerIcon(color,svg,size) {

  return new L.Icon({
    className: "saman_bank",
    //html: svg,
    iconUrl: svg,
    iconSize: [size, size+3],
    iconAnchor: [0, 15],
    popupAnchor: [10, -16]
  });
}

const center = [35.718607293522304,51.393920408129134];

function pointToLayer(feature, coordinates) {
  //console.log(feature.properties.add_);
  //return L.marker(coordinates, { icon: customMarkerIcon(), opacity:0.5 });
  switch (feature.properties.add_){
    case undefined:
      return L.marker(coordinates, { icon: customMarkerIcon('#179dd4',"assets/pin.png",40), opacity:0.8 });
    default:
      return L.marker(coordinates, { icon: customMarkerIcon('#179dd4',"assets/bank.png",25), opacity:1});

  }
  
//   switch (feature.properties['place-details-order']) {
//       case 'Basilian':
//           return L.marker(latlng, { icon: orthodoxCross });
//       case 'Augustinian Canons':
//           return L.marker(latlng, { icon: simpleCrossBlue });
//       default:
//           return L.marker(latlng, { icon: simpleCrossBlack });
//   }
}

 
    

function onEachFeature(feature, layer) {
  //var popup = new L.popuptable;
  //console.log(feature.properties)
  
  //layer.bindPopup(popup);

  layer.bindPopup(table_v_generator(feature.properties));
  layer.on('click', function (e) {
    //e.target.options.setStyle({opacity:1});
  });
}

const MapWrapper = () => {
  const [map, setMap] = useState(null);

  useEffect(() => {
    if (!map) return;

    const legend = L.control({ position: "bottomleft" });

    legend.onAdd = () => {
      const div = L.DomUtil.create("div", "legend");
      div.innerHTML = `click on polygon`;
      return div;
    };


    map.on('moveend', function(e) {
      var bounds = map.getBounds(); //use this bounds
      console.log(bounds)
   });
   

    //legend.addTo(map);
  }, [map]);


  return (
    <MapContainer
      className="markercluster-map"
      whenCreated={setMap}
      center={center}
      zoom={12}
      maxZoom={18}
      minZoom={12}
      scrollWheelZoom={true}
      fullscreenControl={true}
      maxBounds={[[35.55, 51.0798],[35.87, 51.65]]}  
      maxBoundsViscosity={0.9}
      
    >
      
      <TileLayer

      maxNativeZoom={17}
      minNativeZoom={12}
      attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
      //url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
      //url="http://a.tile.stamen.com/toner/{z}/{x}/{y}.png"
      //url="https://cartodb-basemaps-{s}.global.ssl.fastly.net/dark_all/{z}/{x}/{y}.png"
      //url="https://server.arcgisonline.com/ArcGIS/rest/services/Canvas/World_Dark_Gray_Base/MapServer/tile/{z}/{y}/{x}.jpg"
      //url="https://maps.geoapify.com/v1/tile/dark-matter-purple-roads/{z}/{x}/{y}.png"
      //url="https://maps.geoapify.com/v1/tile/toner-grey/{z}/{x}/{y}.png"
      //url="https://api.maptiler.com/maps/streets/{z}/{x}/{y}.png"
      //url="https://hotspot-tileserver.helium.wtf/public.h3_res8/{z}/{x}/{y}.pbf"
      //url={'assets/map_tiles/{z}/{x}/{y}.png'}
      url={'assets/new_maptiles/{z}/{x}/{y}.png'}

    />

      <MarkerClusterGroup
      removeOutsideVisibleBounds ={true}
      showCoverageOnHover={false}
      spiderfyOnMaxZoom={false}
      disableClusteringAtZoom={17}
      maxClusterRadius={100}
      
      >
      <GeoJSON data={geojson_markers} onEachFeature={onEachFeature} pointToLayer={pointToLayer} />
      </MarkerClusterGroup>

      <GeoJSON data={geojson_bank} onEachFeature={onEachFeature} pointToLayer={pointToLayer}/>


    </MapContainer>
  )

}

export default MapWrapper;





